.input-card{
  background-color: white;
  padding: 1rem;
  border-radius: 5px;

  .input-card-container{
    width: 100%;
    margin: auto;

    .input-text{
      width: 100%;
      height: 4rem;

      background-color: white;
      padding: 0.5rem;
      border: none;
      font-size: 15px;
      border-bottom: 1px solid #ccc;
      resize: none;
    }
  }

  .confirm{
    width: 268px;
    margin: 0.2rem auto;
  
    display: flex;
    align-items: center;
  
    .button-confirm{
     height: 2rem;
     width: 6rem;
     background-color: #525252;
     cursor: pointer;
     border: none;
     border-radius: 7px;
     color: #fff;
     font-weight: bold;
     margin-right: 0.5rem;

     &:hover{
    background-color: #f66cc1;
  }
  }
  
  }
  
  .button-cancel{

    background-color: none;
    border: none;
    cursor: pointer;

    svg{
      transition: color 0.3s;
      &:hover{
        color:#f66cc1 ;
      }
    }
    
  }
}

