

nav{
  background: #e2e2e2;
  height: 4rem;
  margin-bottom: 1rem;
  position: static;

  .container{
    width: 100%;
    height: 100%;
    margin: 0 auto;
    padding: 0 1.5rem;
    display: flex;
    align-items: center;

    div{
      display: flex;
      align-items: center;
      flex-grow: 1;

      svg{
        height: 2rem;
        width: 2rem;
        margin-right: 0.3rem;
        color: rgb(243, 99, 154);
      }

      h1{
        font-size: 1.5 rem;
        font-size: large;
        color: #2e2d2d;
      }
    }
  }
}